import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import React, {createContext} from 'react';
import UserStore from "./store/UserStore";
import { I18nextProvider } from 'react-i18next';
import i18n from 'i18next';
import Backend from 'i18next-xhr-backend';
import LanguageDetector from 'i18next-browser-languagedetector';

export const Context = createContext(null)

const LANG_LOCAL_STORAGE_KEY = 'lang';

i18n
  .use(Backend)
  .use(LanguageDetector)
  .init({
    fallbackLng: 'ru',
    debug: true,
    backend: {
      loadPath: './locales/{{lng}}/{{ns}}.json'
    },
    interpolation: {
      escapeValue: false
    },
    detection: {
      order: ['localStorage', 'navigator'],
      lookupLocalStorage: LANG_LOCAL_STORAGE_KEY,
    }
  });

export const changeLanguage = (language) => {
  i18n.changeLanguage(language);
  localStorage.setItem(LANG_LOCAL_STORAGE_KEY, language);
};
const lang = localStorage.getItem(LANG_LOCAL_STORAGE_KEY);
console.log(lang)
const restoreLanguage = () => {
  const lang = localStorage.getItem(LANG_LOCAL_STORAGE_KEY);
  if (lang) {
    i18n.changeLanguage(lang);
  }
};

restoreLanguage();

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <I18nextProvider i18n={i18n}>
    <Context.Provider value={{
      user: new UserStore(),
    }}>
      <App changeLanguage={changeLanguage}/>
    </Context.Provider>
  </I18nextProvider>,
);

reportWebVitals();
