import { Link } from 'react-router-dom';
import exnernalink from '../../../assets/icon/External_Link.png'
import { useTranslation } from 'react-i18next';

function Categories() {
    const { t } = useTranslation();
    return (
        <>
        <div className="container">
            <div className="categories">
            <h1 className="categories-main-text">{t('home.categories.categories-main-text')}</h1>
            <p className="categories-under-text">{t('home.categories.categories-under-text.one')}
                <Link to='/work'>{t('home.categories.categories-under-text.two')}</Link>
            </p>
                <div className="quar">
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.one')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div>
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.two')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div>  
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.three')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div> 
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.fo')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div>   
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.five')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div>  
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.six')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div>  
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.seven')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div>  
                    <div class="feature">
                        <span className="categories-main">{t('home.categories.feature.eight')}</span>
                        <span className="categories-under">2055 специалистов</span>
                    </div>  
                </div>
                <span className="categories-text-under">
                    <Link to="/work">{t('home.categories.categories-text-under')}</Link>
                    <img
                        src={exnernalink}
                        alt='des'
                        width={24}
                    />
                </span>
            </div>
          </div>
        </>
    );
}
  
export default Categories;
  