import { Link } from "react-router-dom";



const Rate = ({listWorked}) => {
    let current = listWorked.wallet.currentTariff
    console.log(current)
    return (
      <div className="history-two">
          <h2>Тариф</h2>
            <div className="history-card-two">
                            <div className="rate-info"> 
                                <div className='history-info'>
                                    <span>{current.tariffType === 0 && <>Нет оплаченого тарифа</>}</span>
                                    <span>{current.tariffType === 1 && <>Базовый</>}</span>
                                    <span>{current.tariffType === 2 && <>Безлимитный</>}</span>
                                </div>
                                <div className="block-current">
                                    <div className="info-text-under">
                                        {current.tariffType === 1 && <>Откликов осталось</>}
                                        {current.tariffType === 2 && <>Подписка действует до</>}
                                    </div>
                                    <div className="info-text-under">{current.tariffLimit}</div>
                                </div>
                            </div>
            </div>
            <div className="link-block">
                <Link to={'/wallet/rate'} className="history-button-two">Выбрать тариф</Link>
            </div>
      </div>
    );
}
export default Rate;